let skillSection = {
    e: document.getElementById('skill'),
    id: 'skill',
    size:{
        width: document.getElementById('skill').offsetWidth,
        height: document.getElementById('skill').offsetHeight
    }
}

let skillSketch = function(p){

    let brain;

    let rightWall = {
        pos: {
            x: skillSection.e.offsetWidth / 2,
            y: 0
        },
        size: {
            width: skillSection.e.offsetWidth / 2
        }

    };

    let circles = [];
    let numbers = [];

    let objAmount;
    let objSize;

    let circleMouse;

    p.setup = () => {

        p.createCanvas(skillSection.e.offsetWidth, skillSection.e.offsetHeight);

        p.background(255);

        circleMouse = new Circle(0, 0, 50);
        
        if (window.innerWidth <= 480){
            brain = new BrainImg(p.loadImage('/img/white-brain.jpg'), 0, 0, 200, 170);
        }else{
            brain = new BrainImg(p.loadImage('/img/white-brain.jpg'),0, 0, 500, 430);
        }

        if (screen.width <= 480) {
            objAmount = 4;
            objSize = 30;
        }else{
            objAmount = 4;
            objSize = 50;
        }

        // Circles
        for (let i = 0; i < objAmount; i++){

            let x = p.random(rightWall.pos.x, (rightWall.pos.x * 2) - 50);
            let y = p.random(0, skillSection.e.offsetHeight);

            let speedX = p.random(-1.5, 1.5);
            let speedY = p.random(-1.5, 1.5);

            if (speedX == 0){
                speedX = 1;
            }

            if (speedY == 0){
                speedY = 1;
            }

            circles[i] = new Circle(x, y, objSize, speedX, speedY);
        }

        // Numbers
        for (let i = 0; i < objAmount; i++){
            let x = p.random(0, rightWall.pos.x - 50);
            let y = p.random(0, skillSection.e.offsetHeight);

            let speedX = p.random(-1.5, 1.5);
            let speedY = p.random(-1.5, 1.5);

            if (speedX == 0) {
                speedX = 1;
            }

            if (speedY == 0) {
                speedY = 1;
            }

            numbers[i] = new Text(i, x, y, objSize, speedX, speedY);
        }

        p.frameRate(60);
    }

    p.draw = () => {

        p.background(255);

        brain.update();
        brain.draw();

        

        p.strokeWeight(4);
        p.stroke(156, 205, 207);

        // circleMouse.draw();

        // Numbers
        for (let i = 0; i < numbers.length; i++) {
            const number = numbers[i];

            number.update();
            number.draw();

            // p.fill(0, 255,0);
            // p.ellipse(number.x, number.y, 5, 5);
        }

        // Circles
        for (let i = 0; i < circles.length; i++){

            let overlapping = false;

            circles[i].noFill();

            circles[i].update();
            circles[i].draw();

            for (let other = 0; other < circles.length; other++){
                if (i != other && circles[i].intersectCircle(circles[other])){
                    overlapping = true;
                }
                
            }

            if (overlapping) {
                circles[i].stroke([0, 98, 255]);

            } else {
                circles[i].stroke([156, 205, 207]);
            }
        }
        
    }

    p.windowResized = () => {

        if (window.innerWidth <= 480) {
            objAmount = 5;
        } else {
            objAmount = 10;
        }
        

        rightWall = {
            pos: {
                x: skillSection.e.offsetWidth / 2,
                y: 0
            },
            size: {
                width: skillSection.e.offsetWidth / 2
            }

        };

        if (window.innerWidth <= 480) {
            brain.width = 200;
            brain.height = 170;
        } else {
            brain.width = 500;
            brain.height = 430;
        }

        if (window.innerWidth >= 1200){
            brain.width = 700;
            brain.height = 630;
        }

        p.resizeCanvas(skillSection.e.offsetWidth, skillSection.e.offsetHeight);
    }

    class Text {
        constructor(text, x, y, size, speedX = 2, speedY = 2) {
            this.text = text;

            this.x = x + 50;
            this.y = y + 50;

            this.size = size;

            this.speedX = speedX;
            this.speedY = speedY;

            this.fillColor = [156, 205, 207];
        }

        draw() {
            p.noStroke();

            p.fill(this.fillColor);
            p.textSize(this.size);
            p.text(this.text, this.x, this.y);

        }

        update() {
            this.x += this.speedX;
            this.y += this.speedY;

            this.intersectWall();
        }

        intersectWall() {
            if (this.x >= rightWall.pos.x - (this.size / 2)) {
                this.x = rightWall.pos.x - (this.size / 2);
                this.speedX *= -1;
            }

            if (this.x <= 0) {
                this.x = 0;
                this.speedX *= -1;

                // this.fillColor = [255, 0, 0];
            }

            if (this.y >= p.height) {
                this.y = p.height;
                this.speedY *=  -1;
            }

            if (this.y - this.size <= 0) {
                this.y = this.size;
                this.speedY *= -1;
            }

        }
    }

    class Circle{
        constructor(x, y, size, speedX=2, speedY=2){
            this.x = x + 50;
            this.y = y + 50;
            
            this.size = size;
            
            this.speedX = speedX;
            this.speedY = speedY;

            this.fillColor = 0;
            this.strokeColor = 0;
        }

        draw(){
            p.stroke(this.strokeColor);
            if (this.noFill){
                p.noFill();
            }else{
                p.fill(this.fillColor);
            }

            p.ellipse(this.x, this.y, this.size, this.size);
            
        }

        update(){
            this.x += this.speedX;
            this.y += this.speedY;

            this.intersectWall();
        }

        noFill(b){
            return b;
        }

        fill(color){
            this.fillColor = color;
        }

        stroke(color){
            this.strokeColor = color;
        }

        intersectCircle(other){
            let d = p.dist(this.x, this.y, other.x, other.y);
            let fd = (this.size / 2) + (other.size /2);

            return d < fd; 
        }

        intersectWall(){
            if (this.x >= p.width - (this.size / 2)) {
                this.x = p.width - (this.size / 2);
                this.speedX = this.speedX * -1;
            }

            if (this.x <= rightWall.pos.x + (this.size / 2)) {
                this.x = rightWall.pos.x + this.size / 2;
                this.speedX = this.speedX * -1;
            }

            if (this.y >= p.height - (this.size / 2)) {
                this.y = p.height - (this.size / 2);
                this.speedY = this.speedY * -1;
            }

            if (this.y <= this.size / 2) {
                this.y = this.size / 2;
                this.speedY = this.speedY * -1;
            }
            
        }
    }

    class BrainImg {
        constructor(img, x, y, width, height) {
            this.img = img;

            this.x = x;
            this.y = y;

            this.width = width;
            this.height = height;
        }

        draw() {
            p.image(this.img, this.x, this.y, this.width, this.height);
        }

        update() {
            this.x = (p.width / 2) - (this.width / 2);
            this.y = (p.height / 2) - (this.height / 2);
        }

        followY(targetY, speedY) {
            if (this.y < targetY) {
                this.y += speedY;
            }

            if (this.y > targetY) {
                this.y -= speedY;
            }
        }
    }
}

let showSkillSketch = new p5(skillSketch, skillSection.id);

const workSection = $('section#work');
const workWrapper = $('.work-wrapper');
const group = $('.work-group');
const workPaginator = $('.work-paginator');

let groupLength = workWrapper.children().length;
let visible = 1;
let index = 0;
let endIndex = (group.length / visible) - 1;
let active = true;





for (let i = 0; i < groupLength; i++) {
    $('.work-paginator ul')
    .append(`
    <li><a href='#0'>${i + 1}</a></li>
    `);
}

    workPaginator.css('top', `-${(workSection.height() / 2) - 70}px`);

    $(window).resize(function(){
        workPaginator.css('top', `-${(workSection.height() / 2) - 70}px`);
    })

    updatePaginatorNumbers(1);  

    $('.work-paginator ul li').click(function () {
        updatePaginatorNumbers($(this).index() + 1);
        
        index = $(this).index();
        leftWork(`-${group.width() * (index)}px`);

    });
    

    $('.work-paginator .icon-arrow-left').click(() => {

        if (active) {
            active = false;

            if (index > 0) {
                previousWork();
            } else {
                endWork();
            }

            window.setTimeout(function () {
                active = true;
            }, 300);
        }

        

        updatePaginatorNumbers(index + 1);
    });

    $('.work-paginator .icon-arrow-right').click(() => {


        if (active) {
            active = false;
            if (index < endIndex) {
                nextWork();
            } else {
                startWork();
            }

            window.setTimeout(function () {
                active = true;
            }, 300);
        }
        

        updatePaginatorNumbers(index + 1);
    });

    function leftWork(pos) {
        group.css({ 'left': pos });
        
    }

    function previousWork() {
        index--;
        group.css({ 'left': `+=${group.width()}` });
    }

    function nextWork() {
       
        index++;
        group.css({ 'left': `-=${group.width()}` });  
 
    }

    function startWork() {
        index = 0;
        group.css({ 'left': '0px' });
    }

    function endWork() {
        index = endIndex;
        group.css({ 'left': `-${group.width() * (groupLength - 1)}px` });
        
    }

    function updatePaginatorNumbers(i) {
        const num = $(`.work-paginator ul li:nth-child(${i})`);

        $('.work-paginator ul li').removeClass('selected');

        num.addClass('selected');
    }






$('a[href*="#"]')
    // Remove links that don't actually link to anything
    .not('[href="#"]')
    .not('[href="#0"]')
    .click(function (event) {
        // On-page links
        if (
            location.pathname.replace(/^\//, '') == this.pathname.replace(/^\//, '')
            &&
            location.hostname == this.hostname
        ) {
            // Figure out element to scroll to
            var target = $(this.hash);
            target = target.length ? target : $('[name=' + this.hash.slice(1) + ']');
            // Does a scroll target exist?
            if (target.length) {
                // Only prevent default if animation is actually gonna happen
                event.preventDefault();
                $('html, body').animate({
                    scrollTop: target.offset().top
                }, 500, function () {
                    // Callback after animation
                    // Must change focus!
                    var $target = $(target);
                    $target.focus();
                    if ($target.is(":focus")) { // Checking if the target was focused
                        return false;
                    } else {
                        $target.attr('tabindex', '-1'); // Adding tabindex for elements not focusable
                        $target.focus(); // Set focus again
                    };
                });
            }
        }
    });
